.social {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;

  padding-right: 60px;
  padding-left: 50px;
  padding-bottom: 18px;
  padding-top: 23px;
  margin: 0;

  list-style: none;
  border-bottom: 1px solid rgba(128, 128, 128, 0.1);

  &__icon-vk,
  &__icon-instagram,
  &__icon-fb {
    fill: #444444;

    &:hover {
      fill: #68b738;
    }

    &:active {
      fill: rgba(104, 183, 56, 0.3);
    }
  }
}

@media (min-width: @tablet-width) {
  .social {
    width: 150px;
    margin-left: 32px;
    padding-top: 13px;
    padding-bottom: 0;

    border: none;
  }

  .social__link--vkontakte svg {
    width: 30px;
    height: 16px;
  }

  .social__link--instagram svg {
    width: 23px;
    height: 23px;
  }

  .social__link--facebook svg {
    width: 14px;
    height: 28px;
  }
}
