.checkbox {
  padding-bottom: 8px;
  padding-top: 15px;

  &:last-child {
    padding-bottom: 10px;
  }
}

.checkbox__label {
  position: relative;

  padding-left: 24px;

  .oswald;
  font-size: 16px;
  font-weight: 400;
  .text-transform;

  &::before {
    .pseudo;
    top: -3px;
    left: -24px;

    width: 30px;
    height: 30px;

    border: 1px solid rgba(128, 128, 128, 0.1);
  }

  &:active {
    color: rgba(68, 68, 68, 0.3);

    &::before {
      opacity: 0.3;
    }
  }
}

.checkbox__control[type="checkbox"] {
  opacity: 0;
}

.checkbox__control[type="checkbox"]:focus ~ .checkbox__label {
  color: rgba(68, 68, 68, 0.3);

  &::before {
    opacity: 0.3;
  }
}

.checkbox__control[type="checkbox"]:checked ~ .checkbox__label {
  &::before {
    .pseudo;

    background-image: url("../img/icon-tick.svg");
    background-size: 21px 16px;
    background-repeat: no-repeat;
    background-position: 5px 6px;
  }
}

@media (min-width: @tablet-width) {
  .checkbox {
    width: 286px;
    padding-bottom: 35px;
    padding-top: 16px;

    &:nth-last-child(2),
    &:last-child {
      padding-bottom: 12px;
    }
  }

  .checkbox__label {
    padding-left: 36px;

    font-size: 20px;
    line-height: 20px;

    &::before {
      top: -4px;
      left: -22px;

      width: 38px;
      height: 38px;
    }
  }

  .checkbox__control[type="checkbox"]:checked ~ .checkbox__label {
    &::before {
      background-position: 10px 10px;
    }
  }
}

@media (min-width: @desktop-width) {
  .checkbox {
    width: 245px;
    padding-right: 0;
  }

  .checkbox__label {
    padding-left: 36px;
    box-sizing: border-box;

    &::before {
      top: -5px;
      left: -23px;
    }
  }
}
