.main-nav__toggle-wrapper {
  position: relative;
}
.main-nav__toggle {
  position: absolute;
  top: -43px;
  right: 20px;
  display: block;
  width: 24px;
  height: 18px;
  background-color: #ffffff;
  border: none;
}
.main-nav__toggle:hover {
  opacity: 0.8;
}
.main-nav__toggle:active {
  opacity: 0.6;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 18px;
  background-image: url("../img/icon-mobile-menu.svg");
  background-repeat: no-repeat;
}
.main-nav--opened .main-nav__toggle {
