  .checkbox {
    width: 286px;
    padding-bottom: 35px;
    padding-top: 16px;
  }
  .checkbox:nth-last-child(2),
  .checkbox:last-child {
    padding-bottom: 12px;
  }
  .checkbox__label {
    padding-left: 36px;
    font-size: 20px;
    line-height: 20px;
  }
  .checkbox__label::before {
    top: -4px;
    left: -22px;
    width: 38px;
    height: 38px;
  }
  .checkbox__control[type="checkbox"]:checked ~ .checkbox__label::before {
    background-position: 10px 10px;
  }
}
@media (min-width: 1300px) {
  .checkbox {
    width: 245px;
    padding-right: 0;
  }
  .checkbox__label {
    padding-left: 36px;
    box-sizing: border-box;
  }
  .checkbox__label::before {
    top: -5px;
    left: -23px;
  }
}
